<template>
<nav-bar>
  <span slot="title" class="title">分类管理 - {{category.name}}</span>
</nav-bar>
<table class="table">
  <tr>
    <td style="width:200px;padding-left:50px;">
      <img @click="inputFileClick" alt="分类头像" class="img-circle" width="100" height="100" v-bind:src="category.avatar" />
      <input @change="uploadAvatar" v-el:avatar class="avatar" type="file" accept="image/*" style="display:none;" />
    </td>
    <td>
      <div class="content-form">
        <form @submit="update">
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">分类名称</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="category.name" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">分类排序</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="排序" type="text" v-model="category.sort" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
            <div class="col-sm-10">
              <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
            </div>
          </div>
        </form>
      </div>
    </td>
  </tr>
</table>
</template>
<script>
  export default {
    data () {
      return {
        category: {
          name: '',
          avatar: '',
          sort: 0
        }
      }
    },
    route: {
      data (transition) {
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/categories/one.json', { access_token: localStorage.token, category_id: params.category_id }).then(function (response) {
          transition.next({
            category: response.data
          })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    methods: {
      inputFileClick (e) {
        return this.$els.avatar.click()
      },
      uploadAvatar (e) {
        e.preventDefault()

        if (this.$els.avatar.value === '') return
        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('category_id', this.$route.params.category_id)
        formData.append('category[avatar]', this.$els.avatar.files[0])

        this.$http.put('http://jiancan.me/api/m1/categories/one.json', formData).then(function (response) {
          this.$set('category', response.data.category)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      update (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('category_id', this.$route.params.category_id)
        formData.append('category[name]', this.category.name)
        formData.append('category[sort]', this.category.sort)

        this.$http.put('http://jiancan.me/api/m1/categories/one.json', formData).then(function (response) {
          this.$route.router.go({ path: '/categories' })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



